import React from 'react';

function Category() {
    const opcions = ["Informació", "Suport", "Treball social", "Atenció", "Assessorament", "Estètica", "Teràpia", "Orientació", "Préstec", "Clínica", "Coixí"];
    return (
        <div className="category row">
            {opcions.map((value, index) => {
                return <a href='# ' className="col-6 btn categoryButton">★ {value}</a>
            })}
        </div>
    )
}

export default Category;